@import url(https://fonts.googleapis.com/css?family=Bitter|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #fff8fb;
  min-height: 100vh;
  color: rgb(59, 2, 31);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #792d57;
}

h1, h3, h4 {
  font-family: 'Bitter', serif !important;
}

h2, p, h5, h6 {
  font-family: 'Open Sans', sans-serif;
}


.icon-logo {
    height: 15vmin;
    pointer-events: none;
    margin: 3em;
    opacity: 1;
  }

.icon-logo {
  -webkit-transition: all 1.5s;
  transition: all 1.5s;
}

.icon-hover a {
  display: block;
}

.icon-hover:hover a {
    height: 16vmin;
}
  

    /* Portrait */
  @media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {

      .icon-logo {
        height: 30vmin;
        pointer-events: none;
        margin: 2em;
      }
      
    }

body, html {
    height: 100%;
}

.container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}  

h1 {
    color: #792d57;
    font-size: 4vh; 

}

.typewriter h2 {
    color: #381428;
    font-size: 2.8vh; 
    white-space: nowrap;
    overflow: hidden;
    letter-spacing: .15em;
    /* width: 30em; */
    -webkit-animation: typing 4s steps(70, end);
            animation: typing 4s steps(70, end); 
    margin-bottom: 40px;
}

/* The typing effect */
@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
}
@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #792d57 ; }
}
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #792d57 ; }
}


